<template>
  <div class="video-wrap">
    ddd
  <video  ref="video" autoplay >
    <source src="http://openstream-beta.mtlab.meitu.com/rolleye/video/index">

  </video>
  </div>
</template>

<script>
  import {doMain} from  'api/url'
  import axios from 'common/js/http';
    export default {
        name: "",
        data(){
            return{

            }
        },
        created(){
          // this.initVideo()
        },
        methods:{
          initVideo(){
              axios.get(doMain+'/rolleye/video/index').then((res)=>{
                console.log(res)
                 this.$refs.video.src=0
              })
          }
        }
    }
</script>

<style scoped lang="scss" rel="stylesheet/scss">
.video-wrap{
  width: 100%;
  height: 200px;
  video{
    width: 100%;
  }
}
</style>



